.item {
  list-style: none;
}

.container {
  padding: 1em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  background: #fff;
  border-radius: 0.3em;
  -webkit-transform: none;
      -ms-transform: none;
          transform: none;
  -webkit-filter: none;
          filter: none;
}
.item:hover > .container {
  -webkit-transform: translate(-1em, 0);
      -ms-transform: translate(-1em, 0);
          transform: translate(-1em, 0);
  -webkit-filter: drop-shadow(0 0 0.2em var(--color0-shade3));
          filter: drop-shadow(0 0 0.2em var(--color0-shade3));
  -webkit-transition: 0.3s -webkit-transform ease-out;
  transition: 0.3s -webkit-transform ease-out;
  -o-transition: 0.3s transform ease-out;
  transition: 0.3s transform ease-out;
  transition: 0.3s transform ease-out, 0.3s -webkit-transform ease-out;
}

.item .button {
  display: none;
}

.item:hover .button {
  display: block;
}

.item:hover .content {
  -o-text-overflow: unset;
     text-overflow: unset;
  white-space: unset;
  overflow-wrap: break-word;
}



