.button {
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  border: none;
  background: none;
  outline: none;
  font-size: 1.5rem;
  -webkit-transition: 0.3s ease-out;
  -o-transition: 0.3s ease-out;
  transition: 0.3s ease-out;
  color: #fff;
  cursor: pointer;
  -webkit-filter: saturate(0);
          filter: saturate(0);
  opacity: 0.5;
}

.button:hover {
  -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
          transform: scale(1.2);
  -webkit-transition: 0.3s ease-in;
  -o-transition: 0.3s ease-in;
  transition: 0.3s ease-in;
  -webkit-filter: saturate(100%);
          filter: saturate(100%);
  opacity: 1;
}

.done {
  background: var(--color1-shade2);
}

.todo {
  background: var(--color2-shade2);
}



