::-webkit-input-placeholder {
  color: #bbb;
  opacity: 1;
}

:-ms-input-placeholder {
  color: #bbb;
  opacity: 1;
}

::-ms-input-placeholder {
  color: #bbb;
  opacity: 1;
}

::placeholder {
  color: #bbb;
  opacity: 1;
}

.input {
  margin: 1rem;
  border: none;
  outline: none;
}

.text {
  height: 1em;
  width: 60%;
  border-bottom: 2px solid var(--color0-shade0);
  -webkit-transition: 0.3s ease-out;
  -o-transition: 0.3s ease-out;
  transition: 0.3s ease-out;
}

.text:focus {
  border-bottom: 2px solid var(--color0-shade1);
  -webkit-transition: 0.3s ease-in;
  -o-transition: 0.3s ease-in;
  transition: 0.3s ease-in;
}

.submit {
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
  font-size: 300%;
  font-family: monospace;
  background: var(--color0-shade1);
  cursor: pointer;
  color: white;
}

.submit:hover {
  background: var(--color0-shade2);
}

.submit:disabled {
  background: var(--color0-shade0);
  cursor: not-allowed;
  -webkit-transition: 0.3s ease-out;
  -o-transition: 0.3s ease-out;
  transition: 0.3s ease-out;
}

.submit:not(:disabled) {
  -webkit-animation: enable 0.3s linear;
          animation: enable 0.3s linear;
  -webkit-transition: 0.3s ease-in;
  -o-transition: 0.3s ease-in;
  transition: 0.3s ease-in;
}

@-webkit-keyframes enable {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  60% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes enable {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  60% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@media (min-width: 600px) {
  .text {
    width: 40%;
  }
}



